<script>
	let bugs = [false, true, false, false, true];
	let solved = false;
	$: chance = [...Array(bugs.length).keys()].slice(1).reduce((a,b) => a*b);


	const debug = (index) => {
		if (bugs[index] === true) return;
		bugs = bugs.map((bug, i) => bug === true ? true : Math.random() > 0.5 && i !== index);
		solved = !bugs.includes(true);
	}
	const addBug = () => {
		bugs = [...bugs, true];
	}

	const deleteBug = () => {
		if (bugs.length == 2 ) {
			alert("Minimum");
			return	
		}
		bugs = bugs.filter((bug, i, t) => i !== t.length -1);
		console.log(bugs)
	} 
</script>

<style>
	.container {

		display: flex;
		justify-content: center;
		align-items: center;
		box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
	}

	.bugs-container {
		width: 50%;
		max-width: 500px;
	}
</style>
<div  class="container">
	<p>Try to debug this...</p>
	<p>You have a  1 / { chance } chance to debug this (to undrstand why - see the code...)</p>
	<div class="bugs-container">
		{#if !solved }
			{#each bugs as bug, i}
				<input type="checkbox" on:change={ () => debug(i) } bind:checked={ bug }>
			{/each}

		{:else}
			<p>'Hooray!!'</p>
			<button type="submit">Play Again...</button>
		{/if}
	</div>
		<button on:click={ addBug }>+</button>
		<button on:click={ deleteBug }>-</button>
</div>
